<style>
  #container {
    /*position: absolute;*/
    float: left;
    width: 795px;
    height: 500px;
    /*top: 50%;*/
    /*left: 50%;*/
    /*margin-left:-400px;*/
    /*margin-top:-250px;*/
    background: #C9C9C9;
  }
  .show-img .ivu-modal-body {
    height: 535px;
  }
  .sensor-list {
    float: left;
    margin-left: 15px;
  }
  .crosshair {
    cursor: crosshair;
  }
</style>
<template>
  <Modal title="查看图片" class="show-img" v-model="isShow" :width="1200" :styles="{top: '20px'}">
  <!--<img :src="server.api.monitor_center.floor.download + imgName" v-if="visible" style="width: 100%">-->
    <div id="container">
    </div>
    <div class="sensor-list">
      <Card style="width:350px">
        <p slot="title">
          <Icon type="ios-film-outline"></Icon>
          当前建筑物/单位探头
      </p>
        <a href="#" slot="extra" @click.prevent="refreshBuildingSensors">
          <Icon type="ios-loop-strong"></Icon>
           刷新
      </a>
        <ul>
          <li v-for="item in buildingSensors">
            <span>
              <!--<img src="../assets/sensor_normal.png"/>-->
              {{ item.name }}
              <a href="#" slot="extra" @click.prevent="addToFloor(item)">
                <Icon type="chevron-down" title="将设备添加到当前楼层"></Icon>
              </a>
            </span>
          </li>
        </ul>
      </Card>
      <Card style="width:350px">
        <p slot="title">
          <Icon type="ios-film-outline"></Icon>
          当前楼层/分区探头
      </p>
        <a href="#" slot="extra" @click.prevent="refreshFloorSensors">
          <Icon type="ios-loop-strong"></Icon>
          刷新
     </a>
        <ul>
          <li v-for="item in floorSensors">
            <span>
               <!--<img src="../assets/sensor_normal.png"/>-->
              {{ item.name }}
              <a href="#" slot="extra" v-if="item.mapping != null && (item.mapping.extendInfo == null || item.mapping.extendInfo.floorX == null) " @click.prevent="locateSensor(item)">
                <Icon type="android-locate" title="点击后在地图上进行设备定位"></Icon>
              </a>
              <a href="#" slot="extra" @click.prevent="removeFromFloor(item)">
                <Icon type="ios-trash" title="删除设备到建筑物中"></Icon>
              </a>
             </span>
          </li>
        </ul>
      </Card>
    </div>
    <div slot="footer">
      <Button type="success" @click="saveChanges"><Icon type="edit"></Icon> 保存</Button>
    </div>
  </Modal>

</template>
<script>
  import server from "../config/api";
  import ol from 'openlayers';
  import Vue from 'vue';

  export default {
    name: 'FloorPlanModal',
    // 子组件参数
    props: ['file','floor','isShow', 'buildingId'],
    data() {
        return {
          center: undefined,
          extent: undefined,
//          sensor_normal_img: require('../assets/sensor_normal.png'),
//          deviceLayer: undefined,
          floorSensors: [],
          buildingSensors: [],
          selectedSensor: undefined,
          locatedSensorIndex: undefined,
          selectedDevice: undefined,
        }
    },
    methods: {
      locateSensor: function(item) {
          this.selectedSensor = item;
          $("#container").addClass("crosshair");
      },
      refreshBuildingSensors: function() {
          alert("TODO 刷新建筑物信息");
      },
      refreshFloorSensors: function() {
          alert("TODO 刷新楼层信息");
      },
      closeFloorModal: function() {
          this.$emit('closeFloorModal');
      },
      drawMap: function() {
        let self = this;
        let imgUrl = server.api.monitor_center.floor.download + this.file.name;
        this.center = ol.proj.transform([121.609643, 38.913305], 'EPSG:4326', 'EPSG:3857');
        // 计算楼层平面图映射到地图上的范围(minX, minY, maxX, maxY)，图片像素为 1000 * 719，保持比例的情况下，把分辨率放大一些
        this.extent = [this.center[0] - 1000 * 1000 / 2, this.center[1] - 719 * 1000 / 2, this.center[0] + 1000 * 1000 / 2, this.center[1] + 719 * 1000 / 2];

        // 设备图标的交互
        var deviceSelect = null;
        var deviceModify = null;
        deviceSelect = new ol.interaction.Select({
          condition: ol.events.condition.singleClick
        });

        deviceModify = new ol.interaction.Modify({
          features: deviceSelect.getFeatures(),
        });
//        deviceModify.on('modifyend', function(e) {
//          var fs = e.features;
//          if(fs.getLength() == 0) {
//            if (deviceSelect != null) deviceSelect.getFeatures().clear();
//            return false;
//          }
//          if (deviceSelect != null) deviceSelect.getFeatures().clear();
//
//          var coordinate = e.mapBrowserEvent.coordinate;
//          if (coordinate[0] < this.extent[0] || coordinate[0] > this.extent[2] || coordinate[1] < this.extent[1] || coordinate[1] > this.extent[3]) {
//            // 超出平面图范围,重新创建设备Feature
//
//          }
//
//          var pixels = [];
//          pixels[0] = (coordinate[0] - this.center[0] + 1000 * 1000 / 2) / 1000;
//          pixels[1] = 719 - (coordinate[1] - this.center[1] + 719 * 1000 / 2) / 1000;
//          console.log('pixels: ' + pixels[0] + ',' + pixels[1]);
//          //updateDeviceLocation(pixels);
//        });

        this.map = new ol.Map({
          view: new ol.View({
            center: this.center,
            zoom: 7
          }),
          controls: [],
          target: 'container'
        });
        this.map.addInteraction(deviceSelect);
        this.map.addInteraction(deviceModify);
        this.map.addLayer(new ol.layer.Image({
          source: new ol.source.ImageStatic({
            url: imgUrl, // 楼层平面图
            imageExtent: this.extent     // 映射到地图的范围
          })
        }));
        Vue.nextTick(()=>{
          // 画点
          this.drawSensors();
        });
        // 注册点击事件
        this.map.on('singleclick', function(value){
          if(self.selectedSensor === undefined) {
              return;
          }
          var x = value.coordinate[0];
          var y = value.coordinate[1];
          // 创建一个设备图标需要的Feature，并设置位置(extentLeft + picX, extentTop + picY)
          var device = new ol.Feature({
            geometry: new ol.geom.Point([x, y]),
            sensorId: self.selectedSensor.id
          });
          // 设置Feature的样式，使用火警图标
          device.setStyle(new ol.style.Style({
            image: new ol.style.Icon({
              src: self.sensor_normal_img
            }),
            text: new ol.style.Text({
              // font: '10px sans-serif' 默认这个字体，可以修改成其他的，格式和css的字体设置一样
              offsetY: -20,
              text: self.selectedSensor.name,
              fill: new ol.style.Fill({
                color: '#19be6b'
              }),
              stroke: new ol.style.Stroke({    //文字边界宽度与颜色
                color: '#fff',
                width: 3
              })

            })
          }));
          // 添加设备Feature到layer上，并把layer添加到地图中
          var anchor = new ol.Feature({
            geometry: new ol.geom.Point([x, y])
          });
          var index = self.floorSensors.indexOf(self.selectedSensor);
          if(self.floorSensors[index].mapping.extendInfo == null) {
            self.floorSensors[index].mapping.extendInfo = {};
          }
          self.floorSensors[index].mapping.extendInfo.floorX = x;
          self.floorSensors[index].mapping.extendInfo.floorY = y;
          var deviceLayer = new ol.layer.Vector({
            source: new ol.source.Vector(),
            extent: self.extent,
            sensorId: self.floorSensors[index].id
          });
          deviceLayer.getSource().addFeature(device);
          self.map.addLayer(deviceLayer);
          // 取消鼠标十字显示，并把locateSensor设为空
          $("#container").removeClass("crosshair");
          self.selectedSensor = undefined;
        });
        deviceSelect.on('select', function(e) {
           if(e.selected.length == 0) {
             self.selectedDevice = undefined;
             return;
           }
//           self.selectedDevice = e.selected[0].getFeatures();
           self.locatedSensorIndex = _.findIndex(self.floorSensors, {'id': e.selected[0].get("sensorId")});
        });
        deviceModify.on('modifyend', function(e) {
          let coordinate = e.mapBrowserEvent.coordinate;
          // 超界，默认代表从图中移除
          if (coordinate[0] < self.extent[0] || coordinate[0] > self.extent[2] || coordinate[1] < self.extent[1] || coordinate[1] > self.extent[3]) {
            self.floorSensors[self.locatedSensorIndex].mapping.extendInfo = null;
            for(let i=0; i<self.map.getLayers().getArray().length; i++) {
              if(self.map.getLayers().getArray()[i].S.sensorId === self.floorSensors[self.locatedSensorIndex].id) {
                self.map.removeLayer(self.map.getLayers().getArray()[i]);
                break;
              }
            }
            return;
          }
          self.selectedDevice = undefined;
          self.floorSensors[self.locatedSensorIndex].mapping.extendInfo.floorX = coordinate[0];
          self.floorSensors[self.locatedSensorIndex].mapping.extendInfo.floorY = coordinate[1];
        });
      },
      // 画探头点
      drawSensors: function () {
        // 画点
        let sensorList = this.floorSensors;
        for(var i=0; i<sensorList.length; i++) {
          if (sensorList[i].mapping == null || sensorList[i].mapping.extendInfo == null || sensorList[i].mapping.extendInfo.floorX == null) {
            // 在楼层中，不在平面图上
            continue;
          }
          let x = sensorList[i].mapping.extendInfo.floorX;
          let y = sensorList[i].mapping.extendInfo.floorY;
          // 创建一个设备图标需要的Feature，并设置位置(extentLeft + picX, extentTop + picY)
          var device = new ol.Feature({
            geometry: new ol.geom.Point([x, y]),
            sensorId: sensorList[i].id
          });
          // 设置Feature的样式，使用火警图标
          device.setStyle(new ol.style.Style({
            image: new ol.style.Icon({
              src: this.sensor_normal_img
            }),
            text: new ol.style.Text({
              // font: '10px sans-serif' 默认这个字体，可以修改成其他的，格式和css的字体设置一样
              offsetY: -20,
              text: sensorList[i].name,
              fill: new ol.style.Fill({
                color: '#19be6b'
              }),
              stroke: new ol.style.Stroke({    //文字边界宽度与颜色
                color: '#fff',
                width: 3
              })

            })
          }));
          // 添加设备Feature到layer上，并把layer添加到地图中
          var anchor = new ol.Feature({
            geometry: new ol.geom.Point([x, y])
          });
          var deviceLayer = new ol.layer.Vector({
            source: new ol.source.Vector(),
            extent: this.extent,
            sensorId: sensorList[i].id
          });
          deviceLayer.getSource().addFeature(device);
          this.map.addLayer(deviceLayer);
        }
      },
      searchFloorSensors: function() {
          if(this.floor.monitorCenterSensorList === undefined || this.floor.monitorCenterSensorList === null) {
            this.floor.monitorCenterSensorList = [];
          }
          this.floorSensors = this.floor.monitorCenterSensorList;
          Vue.nextTick(()=>{
            this.drawMap();
          });
//        this.$http.get(server.api.monitor_center.floor.sensor.find.replace('{floorId}', this.floorId)).then((response)=>{
//          // 查询在某一个楼层/分区的设备列表，返回值类型是MonitorCenterSensor
//          // 其中的mapping列为MonitorCenterSensorMapping类型，所以如果更新楼层信息，都需要更新：
//          // this.floorSensors.floor.id
//          // this.floorSensors.mapping.floorId
//          this.floorSensors = response.body.data;
//          Vue.nextTick(()=>{
//            this.drawMap();
//          });
//        },(error)=>{
//          this.$Message.error({title:'读取当前位置监控设备列表失败.'});
//        });
      },
      searchBuildingSensors: function() {
        this.$http.get(server.api.monitor_center.building.sensor.find.replace('{buildingId}', this.buildingId) + "?noFloor=true").then((response)=>{
          // 查询不在任何楼层中的建筑物下探头
          this.buildingSensors = response.body.data;
        },(error)=>{
          this.$Message.error({title:'读取设备列表失败.'});
        });
      },
      addToFloor: function(item) {
          this.buildingSensors.splice(this.buildingSensors.indexOf(item), 1);
          if(item.floor == null) {
              item.floor = {};
          }
          item.floor.id = this.floor.id;
          // 修改mapping中的数据
          item.mapping.floorId = this.floor.id;
          this.floorSensors.push(item);
      },
      removeFromFloor: function(item) {
        this.floorSensors.splice(this.floorSensors.indexOf(item), 1);
        item.floor = null;
        // 修改mapping中的数据
        item.mapping.floorId = null;
        item.mapping.extendInfo = null;
        this.buildingSensors.push(item);
        // 删除地图上的设备
        for(let i=0; i<this.map.getLayers().getArray().length; i++) {
          if(this.map.getLayers().getArray()[i].S.sensorId === item.id) {
            this.map.removeLayer(this.map.getLayers().getArray()[i]);
            break;
          }
        }
      },
      saveChanges: function() {
          // TODO update floorSensors
          // TODO update buildingSensors
          // 将楼层和建筑物的数据拼接在一起，进行update
        let monitorCenterSensorList = this.buildingSensors;
        Array.prototype.push.apply(monitorCenterSensorList, this.floorSensors);
        this.$http.put(server.api.monitor_center.floor.updateSensors, monitorCenterSensorList).then((response) => {
          this.$Message.success('编辑成功!');
          $("#container").empty();
          this.closeFloorModal();
          // TODO close modal
        }, (error) => {
          this.$Message.error('获取信息失败！');
        });
      }
    },
    watch: {
      isShow: function(val, oldVal){
        if (val == true) {
          // 父组件查询的建筑物中探头列表属于分页后的数据，并且这里查询没在楼层中的探头，所以需要重新查询
          this.searchBuildingSensors();
          this.searchFloorSensors();

        } else {
          // 如果是关闭了，需要给父组件传递消息，否则父组件的modal参数一直为true
          $("#container").empty();
          this.closeFloorModal();
        }
      },
    }
  }
</script>



