<script>
  import common from '../../../libs/common';
  import FloorPlanModal from '../../../components/floor-plan.vue';
  import MapLocatorModal from '../../../components/map-locator2.vue';
  import server from "../../../config/api";
  import ICol from "../../../../node_modules/iview/src/components/grid/col.vue";
  import AddSensorsModal from '../../../components/add-sensor.vue';
  import ol from 'openlayers';
  import Vue from 'vue';
  import niceScroll from 'jquery.nicescroll';

  export default {
    data() {
      const validateMobile = (rule, value, callback) => {
        if(value === undefined || value === null || value.length === 0) {
          callback(new Error('阀值不能为空'));
        }
        let re = /^[0-9]+.?[0-9]*/;
        if (!re.test(value)) {
          callback(new Error('阀值只能输入数字'));
        } else {
          callback();
        }
      };
      return {
        typeCode:undefined,
        numberRule: [
          {validator: validateMobile, trigger: 'blur'}
        ],
        canReturn: this.getYunSessionLength('route_list') > 1,
        deviceId:undefined,
        cameraFloorId:'',
        buildingCameras:[],
        baseCamerasByDevice:[],
        cameraModal:false,
        probeId:undefined,
        defaultFloorId: undefined,
        organizationId: '',
        pageSize: 20,
        pageSize1: 5,
        pageNum: undefined,
        pageNum1: undefined,
        delDevice: {},
        modal: false,
        devicesNameParam: '',
        buildingIdParam: '',
        floorIdParam: '',
        center: undefined,
        extent: undefined,
        map: undefined,
        deviceLayer: undefined,
        iotGatewayList1: [],
        iotGatewayList2: [],
        floorDeviceList: [],//用于设备描点
        device_fire: require('../../../assets/sensor/sensor_alarm.png'),
        device_fault: require('../../../assets/sensor/sensor_fault.png'),
        device_shield: require('../../../assets/sensor/sensor_normal.png'),
        device_warn: require('../../../assets/sensor/sensor_warn.png'),
        device: {
          unit:'',
          iotGatewayId:'',
          extInfo:{
            cameraType:1,
            cameraUid:'',
            cameraPwd:'',
            cameraUrl:'',
          },
        },
        selectedDevice: undefined,
        buildingList: [],
        buildFloors: [],
        deviceTitle: '',
        pageTotal: undefined,
        pageTotal1: undefined,
        addDeviceModal: false,
        deviceTypeList: [],
        buildingFloor: {},
        building: {},
        editDeviceModal: false,
        floor: {},
        tabName: undefined,
        columns: [
          {
            title: '序号',
            type: 'index',
            align: 'center',
            width: 50
          },
          {
            title: '类型',
            key: 'typeName',
            align: 'center',
          },
          {
            title: '名称',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('a', {
                  on: {
                    click: () => {
                      this.openDevice(params.row);
                    }
                  }
                }, params.row.deviceName)
              ]);
            }
          },
          {
            title: '建筑',
            key: 'buildingName',
            align: 'center'
          },
          {
            title: '楼层/分区',
            key: 'floorName',
            align: 'center'
          },
          {
            title: '安装位置',
            key: 'location',
            align: 'center'
          },
          {
            title: '操作',
            align: 'center',
            width: 80,
            render: (h, params) => {
              if (this.editable) {
                return h('div', [
                  h('i-button', {
                    props: {
                      type: 'text',
                      size: 'small'
                    },

                    style: {
                      color: '#f9caca'
                    },
                    on: {
                      click: () => {
                        this.openDelDevices(params.row)
                      }
                    }
                  }, '移除')
                ]);
              }
            }
          }
        ],
        cameraColumns: [
          {
            title: '序号',
            type: 'index',
            align: 'center',
            width: 50
          },
          {
            title: '摄像头名称',
            key: 'cameraName',
            align: 'center',
          },
          {
            title: '位置',
            key:'loaction',
            align: 'center',
            render: (h, params) => {
              var floorName=params.row.floorName;
              var location=params.row.location;
              if((floorName!=null && floorName!='')&&(location!=null && location!='')){
                return floorName+' - '+location;
              } else if((floorName==null || floorName=='') && (location!=null && location!='')){
                return location;
              } else if((floorName!=null && floorName!='') && (location==null || location=='')){
                return floorName;
              } else{
                return '';
              }
            }
          },
          {
            title: '操作',
            align: 'center',
            width: 80,
            render: (h, params) => {
              let binding = params.row.binding;
              if(binding){
                  return h('div', [
                    h('i-button', {
                      props: {
                        type: 'text',
                        size: 'small'
                      },

                      style: {
                        color: '#f9caca'
                      },
                      on: {
                        click: () => {
                          this.confirm(params.row,2)
                        }
                      }
                    }, '解绑')
                  ]);

              } else {
                return h('div', [
                  h('i-button', {
                    props: {
                      type: 'text',
                      size: 'small'
                    },

                    style: {
                      color: '#f9caca'
                    },
                    on: {
                      click: () => {
                        this.custom(params.row)
                      }
                    }
                  }, '绑定')
                ]);
              }
            }
          }
        ],
        devices: [],
        deviceValidate: {
          deviceName: [
            {required: true, message: '设备名称不能为空', trigger: 'blur'}
          ],
          deviceType: [
            {required: true, message: '请选择设备类型', trigger: 'change'}
          ],
          buildingId: [
            {required: true, message: '请选择建筑物', trigger: 'change'}
          ],
          floorId: [
            {required: true, message: '请选择楼层', trigger: 'change'}
          ],
        },
        currentPlanformUrl: null,
      };
    },
    computed: {
      editable: function () {
        return this.hasPermission(this.$YDAuthCode.NormalEntity.BuildingMgmt);
      }
    },
    methods: {
      changeTypeCode: function(){
        if(this.$refs['iotGateway'] !== undefined){
          this.$refs['iotGateway'].resetField();
        }
        if(this.$refs['iotDevice'] !== undefined){
          this.$refs['iotDevice'].resetField();
        }
        if(this.$refs['unit'] !== undefined){
          this.$refs['unit'].resetField();
        }
        if(this.$refs['upLimit'] !== undefined){
          this.$refs['upLimit'].resetField();
        }if(this.$refs['downLimit'] !== undefined){
