        if (this.defaultFloorId !== floorId) {
          this.device.posX = null;
          this.device.posY = null;
          this.defaultFloorId = undefined;
        } else {
          this.currentPlanformUrl = this.device.planformUrl;
        }
        if ((this.device.posX === undefined || this.device.posX === null) && this.currentPlanformUrl !== null && this.currentPlanformUrl !== undefined) {
          // 设备未定位，初始化定位状态
          $("#device-container").addClass("cursor-device");
          this.selectedDevice = this.device;
        }
        if (this.currentPlanformUrl != undefined && this.currentPlanformUrl != null) {
          //新设备追加场合
//          if(this.device.deviceId===undefined || this.device.deviceId===null){
//            //坐标点重置成一个固定的坐标点，左上角
//            this.device.posX=null;
//            this.device.posY=null;
//            //url重置成该楼层的url，如果没有楼层平面图，那么就重置成固定的图片。
//            this.device.planformUrl=planformUrl;
//          }
          this.floorDeviceList = [];
          this.floorDeviceList.push(this.device);
          Vue.nextTick(() => {
            this.drawFloorMap('device-container', server.api.img.replace('{imageId}', this.currentPlanformUrl));
          });
        } else {
          $("#device-container").empty();
          $("#device-container").addClass("no-img");
        }
