    },

    watch: {
      addDeviceModal: function (val, oldVal) {
        if (!val) {
          let deviceContainer = $("#device-container");
          deviceContainer.empty();
          deviceContainer.removeClass("cursor-device");
          this.selectedDevice = undefined;
          this.device.floorId = undefined;
        }
      },

      baseCamerasByDevice:function () {
        if(this.baseCamerasByDevice.length>5){
          $('#baseCamerasByDevice').addClass('baseCameras');
          Vue.nextTick(() => {
            $(".baseCameras").niceScroll({
              cursorcolor: "rgb(99, 114, 131)",
              cursorwidth: "4px",
              cursoropacitymax: 0.4,
              cursorborder: "",
              cursorborderradius: "",
              hidecursordelay: 1000,
            });
          });
        } else {
          $('#baseCamerasByDevice').removeClass('baseCameras');
        }
      }

    },
    mounted() {
      let page = this.topYunSession('route_list');
      if(page === null || page.orgId === undefined) {
        // 根节点
        this.organizationId = localStorage.current_org_id;
      } else {
        this.organizationId = page.orgId;
      }
      this.loadBuildingDevices();
      Vue.nextTick(() => {
        $(".auto-height-table .ivu-table-body").niceScroll({
          cursorcolor: "rgb(99, 114, 131)",
          cursorwidth: "7px",
          cursoropacitymax: 0.4,
          cursorborder: "", // css definition for cursor border
          cursorborderradius: "",
          hidecursordelay: 1000,
        });
        $(".select-type .ivu-select-dropdown").niceScroll({
          cursorcolor: "rgb(99, 114, 131)",
          cursorwidth: "4px",
          cursoropacitymax: 0.4,
          cursorborder: "",
          cursorborderradius: "",
          hidecursordelay: 1000,
        });
        if(this.$route.query.showDeviceModal === 'true') {
          this.add();
        }
      });
    }

  }
</script>



