/* 添加探头共通弹出层 */
<style>
  /* 覆盖checkbox显示文字样式 */
  /*.ivu-checkbox-group-item :nth-child(2) {*/
    /*display: none;*/
  /*}*/
  /* checkbox不显示label内容 */
  /* 兼容ie */
  /*.ivu-checkbox-wrapper span:first-child+span {*/
    /*display: none !important;*/
  /*}*/
  .confirm-span {
    color: #666666;
    cursor: pointer;
    margin-left: 15px;
  }
  .confirm-span:hover {
    color: #2d8cf0;
  }
  .confirm-span-warn:hover {
    color: #ff9900 !important;
  }
</style>
<template>
  <div>
    <Modal title="添加设备" v-model="isShow" :width="1000" :styles="{top: '20px'}">
      <Row>
        <Col span="5">
        <Select v-model="selectedMonitorCenter" filterable placeholder="选择监控中心进行检索" v-if="addType !== 'F'">
          <Option :value="'ALL'">全部</Option>
          <Option :value="'NONE'">不在任何监控中心下的设备</Option>
          <Option v-for="item in monitorCenters" :value="item.id" >{{ item.name }}</Option>
        </Select>
        </Col>
        <Col span="1">&nbsp;</Col>
        <Col span="5">
        <Select v-model="selectedBuilding" filterable placeholder="选择建筑物进行检索" v-if="addType !== 'F'">
          <Option :value="'ALL'">全部</Option>
          <Option :value="'NONE'">不在任何建筑物下的设备</Option>
          <Option :value="myBuilding.id" disabled>{{ myBuilding.buildingName }}</Option>
          <Option v-for="item in buildings" :value="item.id" >{{ item.buildingName }}</Option>
        </Select>
        </Col>
        <Col span="1">&nbsp;</Col>
        <Col span="5">
        <Input v-model="searchParam" placeholder="请输入查询内容" />
        </Col>
        <Col span="1">&nbsp;</Col>
        <Col span="3">
        <Button type="primary" icon="ios-search" @click="searchSensorsByParam" style="margin-left: 5px; width: 80px;">搜索</Button>
        </Col>
      </Row>
      <Row>
        <div style="border-bottom: 1px solid #e9e9e9;padding-bottom:6px;margin-bottom:6px;">
          <!--<Checkbox-->
            <!--:indeterminate="indeterminate"-->
            <!--:value="checkAll"-->
            <!--@click.prevent.native="handleCheckAll">全选</Checkbox>-->
        </div>
        <Checkbox-group v-model="checkAllGroup">
          <Table :columns="sensorColumns" :data="sensors" border>

          </Table>
        </Checkbox-group>
        <div style="margin: 10px;overflow: hidden">
          <div style="float: right;">
            <Page @on-change="searchSensors" :total="pageTotal" :current="1"></Page>
          </div>
        </div>
      </Row>


      <div slot="footer">
        <Button type="success" @click="addSensors"><Icon type="edit"></Icon> 保存</Button>
      </div>
    </Modal>
    <Modal v-model="otherBuildingSensorModal" title="添加设备">
      <Row>
        <span>正在添加其他建筑物设备</span>
      </Row>
      <Row style="margin-top: 5px; margin-bottom: 5px;">
        <span v-for="(sensor, index) in otherBuildingSensorList" style="color: #2d8cf0;">[{{ sensor.name }}]<span v-if="index !== otherBuildingSensorList.length-1">、</span></span>
      </Row>
      <Row>
        <span>到当前建筑物</span>
      </Row>

      <div slot="footer" style="line-height: 2;">
        <Row>
          <span class="confirm-span confirm-span-warn" @click="addGivenSensors('all')"><Icon type="edit" style="margin-right: 5px;"></Icon>强制拖拽设备到当前建筑物</span>
        </Row>
        <Row>
          <span class="confirm-span" @click="addGivenSensors('noBuilding')"><Icon type="edit" style="margin-right: 5px;"></Icon>只保存无建筑物设备</span>
        </Row>
      </div>
    </Modal>
  </div>
</template>
<script>
  import server from "../config/api";
  import common from '../libs/common';

  export default {
      name: 'AddSensorsModal',
      props: ['addType', 'isShow', 'buildingId'],
      data() {
          return {
            otherBuildingSensorModal: false,
            otherBuildingSensorList: [],
            common: undefined,
            checkAllGroup: [],
            searchGroup: [],
            monitorCenterId: undefined,
            myBuilding: {},
            selectedMonitorCenter: 'ALL',
            selectedBuilding: 'ALL',
            monitorCenters: [],
            buildings: [],
            sensors: [],
            searchParam: undefined,
            switchType: undefined,
            pageSize: 7,
            pageTotal: undefined,
            sensorColumns: [
              {
                title: '',
                align: 'center',
                width: 50,
                render:(h,params)=>{
                  let disabled = false;
                  if(!this.common.isObjEmpty(params, 'params.row.building') && this.buildingId ===  params.row.building.id) {
                      disabled = true;
                  }
                  return h('Checkbox',{
                      props: {
                        label: params.row.id,
                        disabled: disabled
                      }
                  });
                }
              },
              {
                title: '设备名称',
                align: 'center',
                render: (h, params) => {
                  return h('div',{
                    style:{
                      textOverflow: 'ellipsis',
                      whiteSpace: 'nowrap',
                      overflow: 'hidden'
                    },
                    domProps: {
                      title:params.row.name
                    }
                  },params.row.name)
                }
              },
              {
                title: '从属建筑物',
                align: 'center',
                render: (h, params) => {
                  if(this.common.isObjEmpty(params, 'params.row.building')) {
                      return '-';
                  } else if(params.row.building.id === this.buildingId) {
                      return h('span',params.row.building.buildingName + '(当前建筑物)');
                  }
                  return h('span',params.row.building.buildingName);
                }
              },
              {
                title: '设备联系人',
                align: 'center',
                render:(h,params)=> {
                  if(params.row.mainContact == null) {
                      return '-';
                  }
                  return h('span', [
                      h('Icon', {
                          props: {
                            type: "ios-person"
                          }
                      }), params.row.mainContact.name
                  ]);
                }
              },
            ],
          }
      },
      methods: {
//          handleCheckAll () {
//            if (this.indeterminate) {
//              this.checkAll = false;
//            } else {
//              this.checkAll = !this.checkAll;
//            }
//            this.indeterminate = false;
//
//            if (this.checkAll) {
//              this.checkAllGroup = this.concatArr(this.checkAllGroup, this.searchGroup);
//            } else {
//              this.checkAllGroup = [];
//            }
//          },
          //合并两个数组，去重
//          concatArr: function(arr1,arr2){
//            //不要直接使用var arr = arr1，这样arr只是arr1的一个引用，两者的修改会互相影响
//            var arr = arr1.concat();
//            //或者使用slice()复制，var arr = arr1.slice(0)
//            for(var i=0;i<arr2.length;i++){
//              arr.indexOf(arr2[i]) === -1 ? arr.push(arr2[i]) : 0;
//            }
//            return arr;
//          },
//          // 数组自去重 原因：
//          // 列表中可能出现两个一样的设备，同一个设备在不同监控中心的不同建筑物上
//          unique: function(arr) {
//            var result = [], hash = {};
//            for (var i = 0, elem; (elem = arr[i]) != null; i++) {
//              if (!hash[elem]) {
//                result.push(elem);
//                hash[elem] = true;
//              }
//            }
//            return result;
//          },
//          checkAllGroupChange (data) {
//            if (data.length === 7) {
//              this.indeterminate = false;
//              this.checkAll = true;
//            } else if (data.length > 0) {
//              this.indeterminate = true;
//              this.checkAll = false;
//            } else {
//              this.indeterminate = false;
//              this.checkAll = false;
//            }
//          },
          addSensors: function() {
            if(!this.ifOthers()) {
              this.$Modal.confirm({
                title: '确认保存',
                content: '<p>即将添加' + this.checkAllGroup.length + '个设备到当前建筑物，是否继续</p>',
                onOk: () => {
                  // TODO selectAll 处理
                  // 插入数据前去重
//                  this.checkAllGroup = this.unique(this.checkAllGroup);
                  this.$http.post(server.api.monitor_center.building.addSensors.replace('{monitorCenterId}', this.monitorCenterId).replace('{buildingId}', this.buildingId), this.checkAllGroup).then((response) => {
                    this.$Message.success('添加成功!');
                    this.closeAddSensorsModal();
                  }, (error) => {
                    this.$Message.error('获取信息失败！');
                  });
                },
              });
            } else {
              // 正在添加其他建筑物的设备
              this.otherBuildingSensorModal = true;
            }
          },
          addGivenSensors: function(string) {
            this.$http.post(server.api.monitor_center.building.addSensors.replace('{monitorCenterId}', this.monitorCenterId).replace('{buildingId}', this.buildingId) + '?addType='+string, this.checkAllGroup).then((response) => {
              this.$Message.success('添加成功!');
              this.otherBuildingSensorModal = false;
              this.closeAddSensorsModal();
            }, (error) => {
              this.$Message.error('获取信息失败！');
            });
          },
          ifOthers: function() {
            // 匹配checkAllGroup和otherBuildingSensorList的交集
            this.otherBuildingSensorList = [];
            for(let i=0; i<this.checkAllGroup.length; i++) {
              for(let j=0; j<this.sensors.length; j++) {
                  if(this.checkAllGroup[i] === this.sensors[j].id) {
                      if(this.sensors[j].building !== null && this.sensors[j].building !== undefined) {
                          this.otherBuildingSensorList.push(this.sensors[j]);
                      }
                      break;
                  }
              }
            }
            return !!this.otherBuildingSensorList.length;
          },
          closeAddSensorsModal: function() {
              this.$emit('closeAddSensorsModal');
          },
          searchMonitorCenters: function() {
            this.$http.get(server.api.monitor_center.getAll).then((response) => {
              this.monitorCenters = response.body.data;
              for (let i=0; i<this.monitorCenters.length; i++) {
                if(this.monitorCenters[i].id == this.monitorCenterId) {
                  this.monitorCenters[i].name = this.monitorCenters[i].name + "【我的】";
                  let monitorCenter = this.monitorCenters[i];
                  this.monitorCenters[i] = this.monitorCenters[0];
                  this.monitorCenters[0] = monitorCenter;
                  break;
                }
              }
            }, (error) => {
              this.$Message.error('获取信息失败！');
            });
          },
          searchBuildings: function() {
            this.$http.get(server.api.monitor_center.building.getAll).then((response) => {
              this.buildings = response.body.data;
              for (let i = 0; i < this.buildings.length; i++) {
                if (this.buildings[i].id == this.buildingId) {
                  this.buildings[i].buildingName = this.buildings[i].buildingName + "【我的】";
                  this.myBuilding.id = this.buildingId;
                  this.myBuilding.buildingName = this.buildings[i].buildingName;
                  this.buildings.splice(i, 1);
                  break;
                }
              }
            }, (error) => {
              this.$Message.error('获取信息失败！');
            });
          },
          searchSensorsByParam: function() {
              this.searchSensors(1);
          },
          searchSensors: function(pageNum) {
            if(this.addType === 'F') {
              // TODO 区分添加
            }
            this.$http.get(server.api.sensors.get + "?pageNum=" + pageNum + "&pageSize=" + this.pageSize + "&monitorCenterId=" + this.$store.state.currentMonitorCenterId + "&searchParam=" + this.searchParam).then((response) =>{
              this.sensors = response.body.data.rows;
              this.pageTotal = response.body.data.total;
              this.searchGroup = [];
              for(let i=0; i<this.sensors.length; i++) {
                  this.searchGroup.push(this.sensors[i].id);
              }
            }, (error) => {
              this.$Message.error('获取信息失败！');
            });
          }
      },
      watch: {
          isShow: function(val, oldVa) {
              if(val == true) {
                this.monitorCenterId =  this.$store.state.currentMonitorCenterId;
                  if(this.addType == 'MC') { // monitor center add sensors
                    // search sensors by your monitor center id
                    this.searchSensors(1);
                  } else if(this.addType == 'F') { // building floor add sensors
                      // TODO search sensors from mapping by your monitor center id
                    this.searchSensors(1);
                  }
                  // search monitor center
//                  this.searchMonitorCenters();
                  // search building
//                  this.searchBuildings();
              } else {
                  // emit close modal
                  this.closeAddSensorsModal();
              }
          }
      },
      created () {
        this.selectedMonitorCenter = this.$store.state.currentMonitorCenterId;
      },
      mounted () {
          this.common = common;
       }
  }
</script>



