  .modal-body {
    border: 1px solid #eee;
    padding: 5px;
    border-radius: 2px;
    margin-bottom: 0px;
    position: relative;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    min-height: 400px;
    height: 400px;
  }

  .modal-body .split {
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 30%;
    border: 1px dashed #eee;
  }

  .right-solid-line {
    border-right: 1px dashed #eee;
  }

  .amap-marker .marker-geo, .amap-marker .marker-geo.amap-marker .marker-fav-single, .amap-marker .marker-marker-plan-poi, .amap-marker .marker-marker-station, .amap-marker .marker-normal, .amap-marker .marker-place, .amap-marker .marker-route, .amap-marker .marker-single {
    background: rgba(0, 0, 0, 0) url("../assets/poi-marker.png") no-repeat scroll -359px -3px / 437px 267px;
    cursor: pointer;
    height: 34px;
    position: absolute;
    width: 25px;
  }
  .amap-marker .marker-select {
    background-position: -140px -4px;
  }
