<template>
  <div>
    <Modal :styles="{top: '20px'}" v-model="isShow" class="default map-locator-modal" width="1000" title="地图定位"
           :loading="isSubmitting" :ok-text="'保存'" @on-ok="submitLocation">
      <Row class="modal-body" :gutter="6">
        <iCol span="8" class="right-solid-line">
          <Row style="height: 390px">
            <Form>
              <Form-item label="GPS定位信息">
                <Row>
                  <iCol span="24"><span>坐标：</span><span>{{selectedLocationLabel}}</span></iCol>
                </Row>
                <Row>
                  <iCol span="24"><span>地址：</span><span>{{selectedAddress.addrAlias}}</span></iCol>
                </Row>
                <Row>
                  <iCol span="8"><span>省：</span><span>{{selectedAddress.addrProvince}}</span></iCol>
                  <iCol span="8"><span>市：</span><span>{{selectedAddress.addrCity}}</span></iCol>
                  <iCol span="8"><span>区/县：</span><span>{{selectedAddress.addrDistrict}}</span></iCol>
                </Row>
                <Row>
                  <iCol span="8"><span>乡/镇：</span><span>{{selectedAddress.addrTownship}}</span></iCol>
                  <iCol span="8"><span>街道：</span><span>{{selectedAddress.addrStreet}}</span></iCol>
                  <iCol span="8"><span>门牌：</span><span>{{selectedAddress.addrStreetNumber}}</span></iCol>
                </Row>
                <Row>
                  <span>社区：</span><span>{{selectedAddress.addrNeighborhood}}</span>
                </Row>
                <Row>
                  <span>大楼：</span><span>{{selectedAddress.addrBuilding}}</span>
                </Row>
              </Form-item>
            </Form>
          </Row>
        </iCol>
        <iCol span="16">
          <Row style="height: 390px">
            <div id="locator_map_container" style="height: 100%;"></div>
            <div id="myPageTop" style="position: absolute; top: 0; width: 200px;">
              <div class="grey ivu-input-wrapper ivu-input-type">
                <input autocomplete="off" spellcheck="false" type="text" placeholder="输入检索位置" class="ivu-input" id="tipinput">
              </div>
            </div>
          </Row>
        </iCol>
      </Row>
      <div slot="footer">
        <Button class="grey cancel" @click="closeModal">取消</Button>
        <Button class="grey" @click="submitLocation">保存</Button>
      </div>
    </Modal>
  </div>
</template>

<script>

  import LOC_GAODE from '../libs/gis/locator-gaode';

  export default {
    name: 'MapLocatorModal',
    props: ['readOnly', 'showModal', 'defaultCenter', 'defaultLocation'],
    data() {
      return {
        isShow: false,
        Locator: LOC_GAODE,
        map: undefined,
        geocoder: undefined,
        isSubmitting: true,
        isInitLoading: true,
        zoom: 12,
        center: [121.5300857178, 38.8626430801],
        searchOption: {
          city: '全国'
        },
        markImg: require('../assets/mark_b.png'),
        selectedMarker: undefined,
        mapSearchResults: [],
        selectedLocation: null,
        selectedAddress: {
          addrAlias: ''
        }

      }
    },
    computed: {
      selectedLocationLabel: function () {
        if (this.selectedLocation !== null) {
          return '[' + this.selectedLocation[0] + ' , ' + this.selectedLocation[1] + ']';
        } else {
          return '未定位';
        }
      }
    },
    methods: {
      closeModal: function () {
        // 需要将关闭响应到父组件
        this.$emit('CloseMapLocatorModal');
      },
      fetchData: function () {
      },
      submitLocation: function () {
        if (this.selectedLocation === null) {
          this.$Message.error("请进行建筑物定位！");
          return;
        }
        this.selectedAddress.location = this.selectedLocation;
        this.$emit('UpdateLocation', this.selectedAddress);
        this.closeModal();
      },
    },
    watch: {
      // 监听是否打开modal，如果打开，进行自动调整弹出层右侧高度
      showModal: function (val, oldVal) {
        this.isShow = val;
      },
      // 监听是否打开modal，如果打开，进行自动调整弹出层右侧高度
      isShow: function (val, oldVal) {
        if (val) {
          $("#tipinput").val('');
          this.Locator.selectedMarker = null;
          this.selectedLocation = null;
          this.selectedAddress = { addrAlias: ''};
          this.$nextTick(() => {
            this.Locator.initMap(this);
          });

        } else {
          // 如果是关闭了，需要给父组件传递消息，否则父组件的modal参数一直为true
          this.closeModal();
        }
      },
    },
    components: {
      // 绑定子组件
    }
  }
</script>



